import { importSPKI, jwtVerify } from 'jose';
import { NextApiRequest, NextApiResponse } from 'next';

import { END_USER_AUTH_COOKIE_KEY } from '@/constants';
import { NEXT_PUBLIC } from '@/env';
import { validateUserInfo } from '@/hooks/useUserInfo/utils';

export default async function handler(req: NextApiRequest, res: NextApiResponse) {
  if (NEXT_PUBLIC.AUTH_ENABLED && process.env.END_USER_AUTH_PUBLIC_KEY) {
    const jwt = req.cookies[END_USER_AUTH_COOKIE_KEY];
    if (!jwt) {
      return res.status(401).json({
        error: 'Unauthorized',
        message: 'No authentication token provided',
      });
    }

    try {
      const publicKey = await importSPKI(process.env.END_USER_AUTH_PUBLIC_KEY, 'EdDSA');
      const { payload } = await jwtVerify(jwt, publicKey);
      if (payload.userInfo && validateUserInfo(payload.userInfo)) {
        return res.json({ user: payload.userInfo });
      }
    } catch (error) {
      return res.status(401).json({
        error: 'Unauthorized',
        message: 'Invalid authentication token',
      });
    }
  }

  return res.status(401).json({
    error: 'Unauthorized',
    message: 'Authentication is required',
  });
}
